* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.btn {
  min-width: 216px;
  min-height: 50px;
  text-align: center;
  letter-spacing: .06em;
  color: #ff0;
  cursor: pointer;
  background-color: #188ce8;
  border: 0;
  border-radius: 4px;
  align-items: center;
  margin: 0 auto;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.88;
  display: block;
}

.btn:hover, .btn:focus {
  color: #188ce8;
  background-color: #ff0;
}

.btn:disabled {
  color: #fff;
  cursor: no-drop;
  background-color: #a9a9a9;
}

